@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

a {
  text-decoration: none; 
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
}

ul {
  list-style: none; 
}
.box-6{
  grid-column: span 6;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 15px;
  transition: all .8s ease;
  -webkit-animation: zoom .8s ease;
          animation: zoom .8s ease;
}
@-webkit-keyframes zoom{
  0%{
    transform: scale(0);
  }
  100%{
    transform: scale(1);
  }
}
@keyframes zoom{
  0%{
    transform: scale(0);
  }
  100%{
    transform: scale(1);
  }
}
.container {
  width: 90%;
  margin: auto; 
  transition: all .8s ease;
}
.page{
  min-height: 100vh;
  display: flex;
  grid-column: span 9;
  transition: all .8s ease;
  overflow-x: hidden;
}
.page-dark{
  color: var(--first);
  background: var(--second);
}
.subtitle {
  grid-column: span 12;
  text-align: left; 
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 15px;
}
#root{
  width: 100%;
  padding: 0px;
  display: grid;
  grid-template-columns: repeat(12,1fr);
}
@media(max-width:1024px){
  .page{
    grid-column: span 12;
    padding-bottom: 25px;
  }
}
@media(max-width:800px){
  .page{
    padding-top: 30px;
    padding-bottom: 80px;
  }
}
:root {
  --first: #fff;
  --second: #121216;
  --third: rgb(25,25,34);
  --primary: #bf4904;
  --complement: rgb(50, 50, 61);
  --font: 'Montserrat', sans-serif;
}
.about-me {
  grid-column: span 12;
  display: flex;
  margin-bottom: 25px; 
  transition: all .8s ease;
  -webkit-animation: opacidad .8s ease;
          animation: opacidad .8s ease;
}
@-webkit-keyframes opacidad{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}
@keyframes opacidad{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}
.info-about-me{
  width: 50%;
}
.label-title{
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  color: var(--primary);
  position: relative;
}
.label-title:after{
  width: 50px;
  height: 3px;
  position: absolute;
  top:8px;
  left:80px;
  background: var(--primary);
  content:"";
}
.gretting{
  text-transform: uppercase;
  margin: 15px 0px;
  font-weight: 600;
  font-size: 32px;
}
.professions{
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 15px;
  text-transform: uppercase;
}
.description-about-me {
  line-height: 1.5;
  font-size: 15px;
  margin-bottom: 15px; 
}
.btn{
  background: var(--primary);
  border-radius: 6px;
  padding: 7px 24px;
  color: var(--first);
  border: 2px solid transparent;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 14px;
  transition: all .3s ease;
  cursor: pointer;
}
.btn:hover{
  background: transparent;
  color: var(--primary);
  border-color: var(--primary);
}

.img-about-me {
  width: 50%; 
  display: flex;
  margin: auto;
  transition: all .8s ease;
  -webkit-animation: derecha .8s ease;
          animation: derecha .8s ease;
  position: relative;
  top:0;
  right:0;
}
 @-webkit-keyframes derecha {
   0%{
    right:-100%;
   }
   100%{
     right: 0%;
   }
 }
 @keyframes derecha {
   0%{
    right:-100%;
   }
   100%{
     right: 0%;
   }
 }
.img-about-me img {
  margin: auto;
  border-radius: 6px;
}
@media(max-width:450px){
  .img-about-me{
    width: 100%;
    margin-bottom: 10px;
  }
}
.menu-bar{
  /* grid-column: span 3; */
  width: 25%;
  position: fixed;
  bottom:0px;
  right:0;
  height: 100vh;
  padding: 25px 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.menu-bar.light-bar{
  background: #f9f9f9;
  border-left: 1px solid #f9f9f9;
}
.menu-bar.light-bar .primera-fila .my-name{
  color: var(--third);
}
.menu-bar.dark-bar{
  background: rgb(25, 25, 34);
  border-left: 1px solid rgb(50, 50, 61);
}
.menu-bar.dark-bar .primera-fila .my-name{
  color: var(--first);
}
.primera-fila{
  padding: 0px 15px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  flex-wrap: wrap;
}
.my-name{
  width: 100%;
  margin: 5px 0px;
}
/*BTN's mode*/
.btn-mode{
  background: #343d5b;
  border-radius: 1000px;
  border:none;
  position: relative;
  cursor: pointer;
  display: flex;
  outline: none;
  transition: all .8s ease;
}
.btn-mode span{
  width: 30px;
  height: 30px;
  line-height: 30px;
  display: block;
  background: none;
  color: #fff;
}
.btn-mode::after{
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  background: #f1f1f1;
  top:0;
  left:0;
  right: unset;
  border-radius: 100px;
  box-shadow: 0px 0px 2px 2px rgba(0,0,0,.2);
  transition: all .8s ease;
}
.dark-mode.active{
  background: orange;
  color:#fff;
}
.btn-mode.active::after{
  left:unset;
  right:0;
}
.my-name .span-dev{
  color: var(--primary);
}
.options-menu .option{
  padding: 7px 15px;
  border-left: 4px solid transparent;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 15px;
  transition: all .8s ease;
}
/*----------*/
.menu-bar.dark-bar .options-menu .option:hover,
.menu-bar.dark-bar .options-menu .option:focus{
  border-color: var(--primary);
  
}
.menu-bar.dark-bar .options-menu .option:hover a,
.menu-bar.dark-bar .options-menu .option:focus a{
  color: var(--primary);
}
.menu-bar.dark-bar .options-menu .option.selected{
  border-color: var(--primary);
}
.menu-bar.dark-bar .options-menu .option.selected a{
  color: var(--primary);
}

.menu-bar.light-bar .options-menu .option:hover,
.menu-bar.light-bar .options-menu .option:focus{
  border-color: var(--primary);
  
}
.menu-bar.light-bar .options-menu .option:hover a,
.menu-bar.light-bar .options-menu .option:focus a{
  color: var(--primary);
}
.menu-bar.light-bar .options-menu .option.selected{
  border-color: var(--primary);
}
.menu-bar.light-bar .options-menu .option.selected a{
  color: var(--primary);
}
/*---------------------*/
.options-menu .option a{
  text-transform: uppercase;
  transition: all .8s ease;
}
.menu-bar.dark-bar .options-menu .option a{
  color: var(--first);
}
.menu-bar.light-bar .options-menu .option a{
  color: var(--third);
}
.social-networks{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.social-networks li{
  font-size: 24px;
  font-weight: 700;
  transition: all .8s ease;
}
.social-networks li a{
  color: var(--primary);
  transition: all .8s ease;
}
.menu-bar.dark-bar .social-networks li:hover a,
.menu-bar.dark-bar .social-networks li:hover,
.menu-bar.dark-bar .social-networks li:focus a{
  color: var(--first);
  transform: scale(1.125);
}
.menu-bar.light-bar .social-networks li:hover a,
.menu-bar.light-bar .social-networks li:hover,
.menu-bar.light-bar .social-networks li:focus a{
  color: var(--second);
  transform: scale(1.125);
}
@media(max-width:1024px){
  .menu-bar{
    width: 100%;
    height: auto;
    padding: 0px;
  }
  .menu-bar .primera-fila{
    position: absolute;
    top: 15px;
    width: auto;
    display: none;
  }
  .menu-bar .primera-fila .btn-mode{
    transform: rotateZ(90deg);
  }
  .my-name{
    display: none;
  }
  .social-networks{
    display: none;
  }
  .options-menu{
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .options-menu .option{
    display: flex;
    flex-direction: column;
    border: none;
    border-left: 0px solid transparent;
    border-top: 4px solid transparent;
    padding-top: 20px 15px;
    margin: 0px 5px;
    margin-bottom: 0px;
    font-size: 20px;
    text-align: center;
  }  
  .options-menu .option:hover,
  .options-menu .option:focus{
    border-left: 0px solid transparent;
    border-color: var(--primary);
    
  }
  .options-menu .option.selected{
    border-top: 4px solid var(--primary);
  }
  .options-menu .option span{
    margin-top: 5px;
    display: block;
    font-size: 10px;
    font-weight: 300;
  }
}
.loader{
  position: fixed;
  top:0;
  left: 0;
  width: 100%;
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  z-index: 10;
  overflow: hidden;
}
.loader.loader-dark{
  background: var(--second);
}
.loader.loader-light{
  background: var(--first);
}
.no-loader{
  display: none;
}
.spinner-box{
  width: 300px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}
/* Alternating Orbits */
.circle-border{
  width: 150px;
  height: 150px;
  padding: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: var(--primary);
  background: linear-gradient(0deg, rgba(63,249,220,0.1) 33%, var(--primary) 100%);
  -webkit-animation: spin .8s linear 0s infinite;
          animation: spin .8s linear 0s infinite;
}
.circle-core{
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.loader.loader-dark .circle-core{
  background-color: var(--second);
}
.loader.loader-light .circle-core{
  background-color: var(--first);
}
/* KeyFrames */
@-webkit-keyframes spin{
  from {
    transform: rotate(0);
  }
  to{
    transform: rotate(359deg);
  }
}
@keyframes spin{
  from {
    transform: rotate(0);
  }
  to{
    transform: rotate(359deg);
  }
}
.container-home{
  display: grid;
  grid-template-columns: repeat(12,1fr);
}
.recent {
  grid-column: span 12;
  display: grid;
  grid-gap: 15px;
  grid-template-columns: repeat(12, 1fr); 
  position: relative;
  top:0;
  left:0;
  transition: all .8s ease;
  -webkit-animation: subir .8s ease;
          animation: subir .8s ease;
}
@-webkit-keyframes subir{
  0%{
    top:100%;
    opacity: 0;
  }
  100%{
    top:0;
    opacity: 1;
  }
}
@keyframes subir{
  0%{
    top:100%;
    opacity: 0;
  }
  100%{
    top:0;
    opacity: 1;
  }
}
.project-resume {
  grid-column: span 3; 
}
.project-resume .portada-project-resume {
  max-width: 200px;
  max-height: 200px;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 5px; 
}
.project-resume .portada-project-resume img {
  width: 100%; 
  border-radius: 6px;
}
.project-resume .name-project-resume {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.7;
  margin-bottom: 3px; 
}
.project-resume .label-project-resume {
  font-size: 12px;
  font-weight: 400;
  line-height: 1.8;
  color: #91919d; 
}

@media(max-width:840px){
  .page .container-home .subtitle{
    display: none;
  }
  .page .container-home .recent{
    display: none;
  }
}
@media(max-width:700px){
  .about-me{
    flex-wrap: wrap;
  }
  .about-me .img-banner{
    width: 100%;
    order: 1;
  }
  .info-about-me{
    width: 100%;
    order: 2;
  }
}

.container-about-me{
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
.icono-tecnologia{
  grid-column: span 3;
  display: flex;
  flex-direction: column;
  text-align: center;
}
#logoExpress{
  padding: 5px;
  background: #fff;
  border-radius: 6px;
}
.description-box{
  grid-column: span 12;
  line-height: 1.5em;
  font-size: 15px;
  margin-bottom: 15px;
}
.icono-tecnologia img{
  width: 40%;
  margin: auto;
}
@media(max-width:700px){
  .box-6{
    grid-column: span 12;
  }
}
.container-portfolio{
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 15px;
}
.project{
  grid-column: span 6;
  border-radius: 6px;
  overflow: hidden;
  border: 1px solid var(--complement);
  transition: all .8s ease;
  transform: zoom 1s ease;
}

@-webkit-keyframes zoom{
  0%{
    transform: scale(0);
  }
  100%{
    transform: scale(1);
  }
}

@keyframes zoom{
  0%{
    transform: scale(0);
  }
  100%{
    transform: scale(1);
  }
}
.project img{
  width: 100%;
}
.front{
  width: 100%;
  overflow: hidden;
  max-height: 210px;
  position: relative;
  top:0px;
  left:0px;
  background: var(--second);
}
.front .tecnologias-usadas{
  width: 100%;
  height: 100%;
  position: absolute;
  top:100%;
  left:0px;
  background: rgba(0, 0, 0, 0.75);
  transition: all .8s ease;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  flex-wrap: wrap;
  padding: 5px;
}
.front .tecnologias-usadas .icono-tec{
  width: 15%;
}
.front .tecnologias-usadas .icono-tec img{
  width: 100%;
}
.front img{
  width: 100%;
  transition: all .8s ease;
}
.front:hover .tecnologias-usadas{
  top:0px;
}
.description-project{
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 15px;
}
.name-and-labels{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 15px;
}
.name-and-labels span{
  border-radius: 4px;
  padding: 2.5px;
  background-color: var(--first);
  color: #000;
  margin-left: 4.5px;
  text-align: center;
}
.text-description{
  margin-bottom: 15px;
}
.buttons-project{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.buttons-project a{
  padding: 5px;
  border:1px solid;
  color: var(--first);
  border-radius: 6px;
  transition: all .8s ease;
  cursor: pointer;
}

.buttons-project a.go-to-page{
  border-color: var(--primary);
  color: var(--primary);
}
.buttons-project a.go-to-repository{
  border-color: var(--primary);
  background-color: var(--primary);
}
.buttons-project a:hover{
  transform: scale(1.075);
}
@media(max-width:840px){
  .container-portfolio{
    padding-bottom: 50px;
  }
  .page .container-portfolio .recent .project-resume{
    grid-column: span 4;
    text-align: center;
  }
  .container-portfolio .project{
    grid-column: span 12;
  }
  .front .tecnologias-usadas .icono-tec{
    width: 10%;
  }
  .page .container-portfolio .recent .project-resume .portada-project-resume {
    margin: auto;
    margin-bottom: 15px;
  }
}
@media(max-width:700px){
  .page .container-portfolio .recent .project-resume{
    grid-column: span 6;
  }
  .page .container-portfolio .recent .project-resume .portada-project-resume{
    max-width: 350px;
    max-height: 350px;
  }
}
@media(max-width:600px){
  .page .container-portfolio .recent .project-resume{
    grid-column: span 12;
  }
  .page .container-portfolio .recent .project-resume .portada-project-resume{
    max-width: 450px;
    max-height: 450px;
  }
}
.container-not-found{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.container-not-found img{
  width: 100%;
}
